<template>
  <div>
    <div class="cardContainer">
      <div
        class=" connnnntainer p-6  max-w-sm bg-white rounded-lg border border-gray-200 shadow-md dark:bg-gray-800 dark:border-gray-700"
      >
        <h5
          class="mb-2 text-2xl font-bold tracking-tight text-dark-vue dark:text-white"
        >
          Flash Game
        </h5>

        <p class="mb-3 p-2 font-normal text-gray-700 dark:text-gray-400">
          On part sur la même base, mais la il est temps de de corcée le défi,
          le timer est régler sur 30 secondes !
        </p>
        <p class="mb-3 font-normal text-gray-700 dark:text-gray-400">
          Perd pas plus de temps et lance la game !
        </p>

        <button
          type="button"
          class="lePetitBoutton text-dark-vue bg-green-vue hover:bg-dark-vue hover:text-white focus:ring-4 focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-blue-600 dark:hover:bg-dark-vue focus:outline-none dark:focus:ring-dark-vue"
          @click="this.modalOpen"
        >
          <i class="fa-solid fa-gauge-simple-high mr-2"></i>
          Partie rapide ?
        </button>
      </div>
    </div>
    <Teleport to="#modal">
      <Transition name="modal">
        <div class="modal-bg" v-if="this.modal" ref="modal">
          <div class="modal">
            <button @click="this.modalOpen" class="bouttttonn">
              <i class="fa-solid fa-xmark"></i>
            </button>
            <div
              class="answerContainer font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2"
            >
                            <h1
                  class="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white"
                >
                  Flash Game
                </h1>
              <div class="score">
                <h1
                  class="mb-2 text-2xl font-bold tracking-tight text-dark-vue dark:text-white"
                >
                  HighScore : {{ this.highScore }}
                </h1>
                <div class="timere">

                    <h1
                      class="mb-2 text-2xl font-bold tracking-tight text-dark-vue dark:text-white"
                    >
                      Timer : {{ this.timer }}
                    </h1>
                </div>
                <h1
                  class="mb-2 text-2xl font-bold tracking-tight text-dark-vue dark:text-white"
                >
                  Score : {{ this.score }}
                </h1>
              </div>


              <div class="answer" v-if="!this.partieLancer">
                <div
                  @click="this.newRapideGame"
                  class="responseee block p-6 max-w-sm bg-white rounded-lg border border-gray-200 shadow-md hover:bg-gray-100 dark:bg-gray-800 dark:border-gray-700 dark:hover:bg-gray-700"

                >
                  <h5
                    class="mb-2 text-2xl font-bold tracking-tight text-dark-vue dark:text-white"
                  >
                    {{ message }}
                  </h5>

                </div>

              </div>
              <div class="answer" v-if="this.partieLancer">
                <div
                  @click="this.checkForAnswer(this.question.reponse[0], 'one')"
                  class="responseee block p-6 max-w-sm bg-white rounded-lg border border-gray-200 shadow-md hover:bg-gray-100 dark:bg-gray-800 dark:border-gray-700 dark:hover:bg-gray-700"
                  :class="
                    this.isOneTrue
                      ? 'text-white bg-green-vue hover:bg-green-light-vue unclickable'
                      : ''
                  "
                >
                  <h5
                    class="mb-2 text-2xl font-bold tracking-tight text-dark-vue dark:text-white"
                    :class="this.isOneFalse ? 'text-red-600 unclickable' : ''"
                    v-if="!this.showResponse"
                  >
                    {{ this.question.reponse[0] }}
                  </h5>
                  <h5
                    class="mb-2 text-2xl font-bold tracking-tight text-dark-vue dark:text-white"
                    :class="this.isOneFalse ? 'text-red-600 unclickable' : ''"
                    v-if="this.showResponse"
                  >
                    {{ this.question.population[0] }}
                  </h5>
                </div>
                <div
                  @click="this.checkForAnswer(this.question.reponse[1], 'two')"
                  class="responseee block p-6 max-w-sm  rounded-lg border border-gray-200 shadow-md hover:bg-gray-100 dark:bg-gray-800 dark:border-gray-700 dark:hover:bg-gray-700"
                  :class="
                    this.isTwoTrue
                      ? 'text-white bg-green-vue hover:bg-green-light-vue unclickable'
                      : ''
                  "
                >
                  <h5
                    class="mb-2 text-2xl font-bold tracking-tight text-dark-vue dark:text-white"
                    :class="this.isTwoFalse ? 'text-red-600 unclickable' : ''"
                    v-if="!this.showResponse"
                  >
                    {{ this.question.reponse[1] }}
                  </h5>
                  <h5
                    class="mb-2 text-2xl font-bold tracking-tight text-dark-vue dark:text-white"
                    :class="this.isTwoFalse ? 'text-red-600 unclickable' : ''"
                    v-if="this.showResponse"
                  >
                    {{ this.question.population[1] }}
                  </h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Transition>
    </Teleport>
  </div>
</template>

<script>
import axios from "axios";

export default {
  components: {},
  data() {
    return {
      modal: false,
      highScore: 0,
      isGameOpen: false,
      isOneTrue: false,
      isOneFalse: false,
      isTwoTrue: false,
      isTwoFalse: false,
      showResponse: false,
      message: "Play !",
      timer: 30,
      regions: [],
      communes: [],
      question: {
        reponse: [],
        population: [],
        correctReponse: "",
      },
      score: 0,
      partieLancer: false
    };
  },

  mounted() {
    if (localStorage.getItem("HighScoreRegionRapide")) {
      this.highScore = localStorage.getItem("HighScoreRegionRapide");
    }
    this.getRegions();
    this.getCommunes();
    this.getDepartements();
  },
  methods: {
    async getRegions() {
      await axios.get("https://geo.api.gouv.fr/regions").then((res) => {
        this.regions = res.data;
        console.log("region", this.regions);
      });
    },
    async getDepartements() {
      await axios.get("https://geo.api.gouv.fr/departements").then((res) => {
        this.departements = res.data;
        console.log("departement", this.departements);
      });
    },
    async getCommunes() {
      await axios.get("https://geo.api.gouv.fr/communes").then((res) => {
        this.communes = res.data;
        console.log("communes", this.communes);
      });

      this.regions.forEach((region) => {
        let numberOfPopulasse = 0;

        for (let i = 0; i < this.communes.length; i++) {
          const commune = this.communes[i];
          if (commune.codeRegion === region.code) {
            // console.log('cest que celles la')
            if (commune.population !== "undefined") {
              // console.log(region.population)
              numberOfPopulasse =
                numberOfPopulasse + parseInt(commune.population);
            }
          }
          region.population = numberOfPopulasse;
        }
      });
    },

    modalOpen() {
      if (!this.isGameOpen) {
        this.score = 0;
      }
      this.modal = !this.modal;
    },

    getRandomInt(max) {
      return Math.floor(Math.random() * max);
    },
    separator(numb) {
      var str = numb.toString().split(".");
      str[0] = str[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return str.join(".");
    },
    newRapideGame(){
        this.score = 0
        this.partieLancer = true
        this.newGame()
        this.countDownTimer()
        
        setTimeout(() => {
            this.message = "Tu veux faire mieux ?"
            this.partieLancer = false
          }, 30000);
        

    },
    newGame() {
        let randomNumberOne = this.getRandomInt(this.regions.length);
      if (this.question.reponse[0] === this.regions[randomNumberOne].nom) {
          randomNumberOne = this.getRandomInt(this.regions.length);
      }

      let randomNumberTwo = this.getRandomInt(this.regions.length);
      if (
          randomNumberOne === randomNumberTwo ||
        this.question.reponse[1] === this.regions[randomNumberTwo].nom
      ) {
          randomNumberTwo = this.getRandomInt(this.regions.length);
      }

      this.question.reponse[0] = this.regions[randomNumberOne].nom;
      this.question.population[0] = this.separator(
          this.regions[randomNumberOne].population
      );
      this.question.reponse[1] = this.regions[randomNumberTwo].nom;
      this.question.population[1] = this.separator(
        this.regions[randomNumberTwo].population
      );

      console.log(this.question.reponse);
      if (
          this.regions[randomNumberOne].population >
        this.regions[randomNumberTwo].population
      ) {
          this.question.correctReponse = this.regions[randomNumberOne].nom;
      } else {
          this.question.correctReponse = this.regions[randomNumberTwo].nom;
      }

    },

    countDownTimer() {
      if (this.timer > 0) {
        setTimeout(() => {
          this.timer -= 1;
          this.countDownTimer();
        }, 1000);
      }
    },
    checkForAnswer(answer, button) {
      this.showResponse = true;
      if (answer === this.question.correctReponse) {
        this.score++;
        if (button === "one") {
          this.isOneTrue = true;
          this.isTwoFalse = true;
        } else if (button === "two") {
          this.isTwoTrue = true;
          this.isOneFalse = true;
        }
      } else if (answer !== this.question.correctReponse) {
        if (button === "one") {
          this.isOneFalse = true;
          this.isTwoTrue = true;
        } else if (button === "two") {
          this.isTwoFalse = true;
          this.isOneTrue = true;
        }
      }
      this.checkHighScore();
      setTimeout(() => {
        this.isOneTrue = false;
        this.isTwoFalse = false;
        this.isTwoTrue = false;
        this.isOneFalse = false;

        this.showResponse = false;
        this.newGame();
      }, 2000);
    },
    checkHighScore() {
      if (localStorage.getItem("HighScoreRegionRapide") < this.score) {
        localStorage.setItem("HighScoreRegionRapide", this.score);
        this.highScore = this.score;
      }
    },
  },
};
</script>

