<template>
  <div>
    <div v-for="region in regions" :key="region.id">
      <DepartementRegionVue
        :regionName="region.nom"
        :regionCode="region.code"
        :regionPopulation="region.population"
        :departements="departements"
        :communes="communes"
      />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import DepartementRegionVue from "../components/DepartementRegionTwo.vue";

export default {
  components: {
    DepartementRegionVue,
  },
  data() {
    return {
      departements: [],
      regions: [],
      communes: [],
    };
  },

  mounted() {
    this.getRegions()
    this.getCommunes()
    this.getDepartements()
  },
  methods: {
    async getRegions() {
      await axios.get("https://geo.api.gouv.fr/regions").then((res) => {
        this.regions = res.data;
        console.log("region", this.regions);
      });
    },
    async getDepartements() {
      await axios.get("https://geo.api.gouv.fr/departements").then((res) => {
        this.departements = res.data;
        console.log("departement", this.departements);
      });
    },
    async getCommunes() {
      await axios.get("https://geo.api.gouv.fr/communes").then((res) => {
        this.communes = res.data;
        console.log("communes", this.communes);
      });

      this.regions.forEach((region) => {
        let numberOfPopulasse = 0;
 
        for (let i = 0; i < this.communes.length; i++) {
          const commune = this.communes[i];
          if (commune.codeRegion === region.code) {
            // console.log('cest que celles la')
            if (commune.population !== "undefined") {
              // console.log(region.population)
              numberOfPopulasse =
                numberOfPopulasse + parseInt(commune.population);
            }
          }
          if(commune.codeDepartement)
          region.population = numberOfPopulasse;
        }
      });
      this.departements.forEach((departement) => {
        let tempArray = [];
        for (let i = 0; i < this.communes.length; i++) {
          const commune = this.communes[i];
          if (commune.codeDepartement === departement.code) {
            
            // let communeToAdd = {name: commune.nom, population: commune.population}
            if (commune.population !== "undefined" || commune.nom !== 'undefined' || commune.codesPostaux !== 'undefined') {
              let thecommune = {name: commune.nom, population: parseInt(commune.population), codesPostaux: commune.codesPostaux}
              tempArray.push(thecommune)
           
            }
          }
          // if(commune.codeDepartement)
          departement.communes = tempArray;
        }
      });
    },
  },
};
</script>

